@import url(https://fonts.googleapis.com/css2?family=Arimo&family=Lato&display=swap);
html,body{font-family:"Lato",sans-serif;height:100%;display:flex;flex-direction:column;background:#f1edee}#root{flex:1 1;display:flex;flex-direction:column}.titlebar h1{font-family:"Arimo",sans-serif;text-align:center;text-transform:uppercase;margin:.65rem;padding:0}.App{display:flex;flex:1 1;flex-direction:column;margin-top:1rem}.btn{background:#3d5467;border:none;margin:1rem;padding:1rem;border-radius:5px;color:#fff}fieldset{position:relative;margin:1rem;background-color:#3d5467;border:none;border-radius:5px;color:#fff}fieldset legend{text-transform:uppercase;font-family:"Arimo",sans-serif;font-size:10px;position:absolute;bottom:100%;background-color:inherit;padding:.25rem .5rem;color:currentColor;border-radius:5px 5px 0 0}.form-control{display:flex;flex-direction:column;background-color:#fff;position:relative;margin:2rem 0rem 0rem 0rem;border-radius:0 5px 5px 5px}.form-control:not(:last-child){margin-bottom:3rem}.form-control label{text-transform:uppercase;font-family:"Arimo",sans-serif;font-size:10px;position:absolute;bottom:100%;background-color:inherit;padding:.25rem .5rem;color:#3d5467;border-radius:5px 5px 0 0}.form-control input,.form-control select{border:none;padding:.25rem;margin:.25rem;outline:none}.unit-toggle{display:inline-flex;align-items:center;margin-top:.5rem;margin-left:auto}.unit-toggle label{font-size:12px;color:#fff}.unit-toggle input{margin-left:.5rem}.result-table{display:flex;flex-direction:column;background-color:#3d5467;margin:0 1rem 0 1rem;margin-top:auto;overflow:hidden;padding:.25rem;border-radius:5px}.result-table table{width:100%;border-collapse:collapse}.result-table table tr,.result-table table td{border:none}.result-table table td{color:#fff;padding:.25rem}.result-table table .text-center{text-align:center}.result-table table .text-right{text-align:right}.result-table table .header{font-family:"Arimo",sans-serif;font-weight:bold;color:#fff;padding:.25rem;font-size:12px}
